import React from 'react';
import Standalone from '../layouts/standalone';
import ListingDetails from '../listing-details';
import Map from '../map';
import Disqus from 'disqus-react';
import Share from '../share'
import SEO from '../seo'
import CategoryIcon from '../category-icon'
import { graphql} from 'gatsby';

const StandardListing = (props) => {
  const { title, description, image, coordinates, tags, ...details } = props.data.markdownRemark.frontmatter;
  const content = props.data.markdownRemark.html;
  const { googleMapsKey, commonKeywords } = props.data.site.siteMetadata;
  const allCategories = props.data.categories.edges.map(edge => edge.node);
  return (<Standalone>
    <SEO title={ title } description={ description } keywords={[...tags, ...commonKeywords]}/>
    <main className="listing mt-5">
      <div className="container-fluid">
        <div className="row">
          <div className="col-md">
            <article className="card">
              {image && (
                <img src={image} className="card-img-top" alt={title}/>
              )}
              <div className="card-body">
                <h1 className="title--header dark medium">{title}</h1>
                {
                        tags
                          .map(tg => allCategories.find(catNode => catNode.frontmatter.tag === tg))
                          .filter(catNode => catNode)
                          .map((ic, idx) => (
                            <CategoryIcon
                              icon={ic.frontmatter.icon}
                              name={ic.frontmatter.name}
                              path={ic.fields.slug}
                              key={idx}/>
                          ))
                }
                <div dangerouslySetInnerHTML={{ __html: content }} />
                <ListingDetails { ...details }></ListingDetails>
                <Map coordinates={coordinates} googleMapsKey={googleMapsKey}></Map>
              </div>
              <ul className="list-group list-group-flush m-0">
                <li className="list-group-item"><em className="tag-list">Tags: {tags.join(', ')}</em></li>
              </ul>
            </article>
            <Share/>
            <Disqus.DiscussionEmbed shortname="expatty" />
          </div>
        </div>
      </div>
    </main>
  </Standalone>)
};

export default StandardListing;

export const pageQuery = graphql`
    query StandardListingBySlug($slug: String!) {
        site {
            siteMetadata {
                googleMapsKey
                commonKeywords
            }
        }
        markdownRemark(fields: { slug: { eq: $slug } }) {
            id
            html
            frontmatter {
                title
                description
                image
                website
                tel
                hours
                email
                address
                price
                coordinates
                tags
            }
        }
        categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
    }
`