import React from 'react'

const ListingDetails = ({
  website,
  email,
  address,
  price,
  tel,
  hours
}) => (
  <dl className="row">
    {
      website && (
        <>
          <dt className="col-xl-1">Website</dt>
          <dd className="col-xl-11"><a className="link" href={ website } target="_blank" rel="nofollow noopener noreferrer">{ website }</a></dd>
        </>
      )}
    {
      tel && (
        <>
          <dt className="col-xl-1">Tel</dt>
          <dd className="col-xl-11"><a className="link" href={ `Tel: ${ tel }` } rel="nofollow">{ tel }</a></dd>
        </>
      )}
    {
      email && (
        <>
          <dt className="col-xl-1">Email</dt>
          <dd className="col-xl-11"><a className="link" href={ `mailto:${ email }` } rel="nofollow">{ email }</a></dd>
        </>
      )}
    {
      hours && (
        <>
          <dt className="col-xl-1">Hours</dt>
          <dd className="col-xl-11">{ hours }</dd>
        </>
      )
    }
    {
      address && (
        <>
          <dt className="col-xl-1">Address</dt>
          <dd className="col-xl-11"><a className="link" href={ `https://maps.google.com/?q=${ address }` }
                                       target="_blank" rel="nofollow noopener noreferrer">{ address }</a></dd>
        </>

      )}
    {
      price && (
        <>
          <dt className="col-xl-1">Price</dt>
          <dd className="col-xl-11"><em>{ price }</em></dd>
        </>
      )}
  </dl>
);

export default ListingDetails;